.blog-icons {
  padding-top: 0 !important;
}
.blog-icons.icon-type-row {
  height: 100%;
}
@media screen and (max-width: 1024px) {
  .blog-icons.icon-type-row {
    justify-content: center;
  }
  .blog-icons.icon-type-row .list-image {
    margin-right: 15px;
  }
  .blog-icons.icon-type-row .icons--item {
    margin-bottom: 25px;
  }
  .blog-icons.icon-type-row .icons--item .list-image {
    text-align: center;
  }
  .blog-icons.icon-type-row .icons--item:last-of-type {
    margin-bottom: 0;
  }
}
@media screen and (max-width: 1199px) {
  .blog-icons .icon-type-slider .slider-wrap {
    padding: 0 15px;
    margin-bottom: 15px;
  }
}
.blog-icons .icon-carousel {
  position: relative;
  max-width: 650px;
  margin: auto;
}
@media screen and (min-width: 1025px) and (max-width: 1199px) {
  .blog-icons .icon-carousel {
    max-width: 500px;
  }
}
.blog-icons .icon-carousel li::before {
  content: unset !important;
}
.blog-icons .icon-carousel .img--wrap {
  width: 75px;
  height: 75px;
  margin-bottom: 35px;
}
.blog-icons .icon-carousel .flex-direction-nav a {
  opacity: 1 !important;
}
.blog-icons .icon-carousel .flex-prev,
.blog-icons .icon-carousel .flex-next {
  width: 47px;
  height: 44px;
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/chevron-slider.svg");
}
.blog-icons .icon-carousel .flex-prev:hover, .blog-icons .icon-carousel .flex-prev:focus,
.blog-icons .icon-carousel .flex-next:hover,
.blog-icons .icon-carousel .flex-next:focus {
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/chevron-slider-hover.svg");
  transform: rotate(180deg);
}
.blog-icons .icon-carousel .flex-next {
  transform: rotate(180deg);
}
.blog-icons .icon-carousel .lSSlideOuter .lSPager.lSpg > li {
  padding: 0 10px;
}
.blog-icons .icon-carousel .lSSlideOuter .lSPager.lSpg > li a {
  height: 18px;
  width: 18px;
}
.blog-icons .icon-carousel .slider-navigation {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 20px;
}
@media screen and (min-width: 1025px) {
  .blog-icons .icon-carousel .slider-navigation {
    margin-bottom: 0px;
    position: absolute;
    transform: translateX(-7.5%);
    width: 118%;
    top: 40%;
  }
}
.blog-icons .icon-carousel .lSAction > a {
  opacity: 1;
}
.blog-icons .icon-carousel .lSPager {
  margin-top: 15px !important;
}
.blog-icons .icon-carousel .slide {
  border-radius: 12px;
  background-color: #17fcc4;
  padding: 24px 10px;
  max-width: 650px;
}
.blog-icons .icon-carousel .slide .quote-img-wrap {
  display: none !important;
}
.blog-icons .icon-carousel .slide .quote-by-info {
  padding-left: 22px !important;
  font-family: 600;
}
.blog-icons .icon-carousel .slide .quote-by-info strong,
.blog-icons .icon-carousel .slide .quote-by-info b {
  font-weight: 600;
}
.blog-icons .icon-carousel .slide .quote-by-info::before {
  background-color: #fff;
}
.blog-icons .icon-carousel .slide .company-name {
  font-family: "Libre Franklin", sans-serif;
}
.blog-icons .icon-carousel .slide .person-img-wrap {
  display: flex;
  justify-content: flex-start;
  align-content: center;
  align-items: center;
  margin-bottom: 15px;
}
.blog-icons .icon-carousel .slide .person-img-wrap .star--rating {
  padding-left: 15px;
}
.blog-icons .icon-carousel .slide .person-img-wrap img {
  border-radius: 50%;
  border: 2px solid #17fcc4;
  width: 56px;
  height: 56px;
}
.blog-icons .icon-carousel .slide .txt-content,
.blog-icons .icon-carousel .slide .txt-content p {
  color: #fff;
}
.blog-icons .icon-columns {
  margin-top: 10px;
}
.blog-icons .icon-columns .list-image {
  margin-right: 15px;
}
.blog-icons .icon-columns .list-image .img--wrap {
  width: 105px;
  height: 88px;
}
.blog-icons .icon-columns .list-content {
  margin-top: 10px;
}
.blog-icons .icon-row {
  display: grid;
  grid-row-gap: 24px;
}
.blog-icons .icon-row .icons--item .list-image .img--wrap,
.blog-icons .icon-row .icons--item .list-image img {
  width: 68px;
  height: 68px;
  object-fit: contain;
}
.blog-icons .icon-row .icons--item .list-title {
  font-family: "Libre Franklin", sans-serif !important;
  font-size: 28px !important;
  font-weight: 700 !important;
  line-height: 30px !important;
}
.blog-icons .icon-row .icons--item .list-text, .blog-icons .icon-row .icons--item .list-text p {
  font-family: "Libre Franklin", sans-serif !important;
  font-size: 24px !important;
  font-weight: 600 !important;
  line-height: 36px !important;
}